.mainWrapper {
height: 100%;
}

.btnTransactionWrapper {
display: flex;
justify-content: space-evenly;
margin: 10px 7px 9px;
}

.btnTransaction {
  display: flex;
  background-color: inherit;
  transition: scale 0.3s ease 0s;
}

.btnTransaction:hover,
.btnTransaction:focus {
  cursor: pointer;
  scale: 1.05;
}

