.addItemForm {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
  }
  
  .addItemLabel{
    padding: 0px 0px 0px 5px;
    margin: 5px;
    flex-grow: 1;
    border-bottom: 1px solid rgb(128, 128, 128);
  }
  
  .addItemInput {
    width: 100%;
    font-size: 16px;
    background-color: transparent;
    border: none;
  
    
  }
  
  .btnAdd {
      background-color: inherit;
      padding: 0px;
      width: 30px;
  }